.navbar {
  background: black;
  height: 80px;

  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
  font-family: 'PT Sans', sans-serif;
}

.navbar-container {
  justify-content: center;
  align-items: center;
  height: 80px;
}

.navbar-logo {
  color: #fff;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  text-align: center;

  justify-content: center;

}

.logo {
  text-align: center;
}

.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 2rem;
  float: right;
}

.nav-item {
  position: relative;
  list-style: none;
}

.nav-links {
  color: transparent;
  position: relative;
  align-items: center;
  text-decoration: none;
  letter-spacing: 2px;
  -webkit-text-stroke: 1px rgba(255, 255, 255, .5);
  font-size: 2em;
  text-transform: uppercase;
  line-height: 1em;
  height: 100%;
}

.nav-links::before {
  content: attr(data-text);
  position: absolute;
  color: rgb(81, 193, 241);
  width: 0;
  overflow: hidden;
  transition: 1s;
}

.nav-links:hover::before {
  width: 100%;
}

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
