.msgBtn{
  letter-Spacing: 2px;
  width: 250px;
  cursor: pointer;
  z-index: 0;
	display: block;
  position: relative;
	padding: 19px 39px 18px 39px;
	color: white;
	margin: auto;
	background: transparent;
	font-Size: 18px;
	text-Align: center;
	font-style: normal;
	width: auto;
  border-radius: 10px;
  border: none;
}

.msgBtn:before {
  content: '';
  background: linear-gradient(45deg, #89CFF0, 	#0096FF, #6495ED, #ADD8E6, #A7C7E7, #CCCCFF, #87CEEB, #51C1F1, #F0FFFF);
  position: absolute;
  top: -2px;
  left:-2px;
  background-size: 400%;
  z-index: -1;
  -webkit-filter: blur(5px);
          filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  -webkit-animation: glowing 20s linear infinite;
          animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity .3s ease-in-out;
  border-radius: 10px;
}

.msgBtn:active {
  color: transparent;
}

.msgBtn:active:after {
  background: transparent;
}

.msgBtn:hover:before {
  opacity: 1;
}

.msgBtn:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: transparent;
  left: 0;
  top: 0;
  border-radius: 10px;
  border: 1px solid rgb(81, 193, 241);
}

@-webkit-keyframes glowing {
  0% { background-position: 0 0; }
  50% { background-position: 400% 0; }
  100% { background-position: 0 0; }
}

@keyframes glowing {
  0% { background-position: 0 0; }
  50% { background-position: 400% 0; }
  100% { background-position: 0 0; }
}

